import React from "react";
import "./index.scss";

const SequenceInput = ({
  register,
  types,
  names,
  placeholders,
  values,
  onChange,
  suffixTexts,
  prefixText = "",
  maxLength,
  disabled = false,
  width = "100%",

}) => {
  return (
    <div className="d-flex sequenceInput">
      <div className="input-group">
      {prefixText && (
          <span className="input-group-text B3-Medium fw-600" id={`addon`}>
            {prefixText}
          </span>
        )}
        {names.map((name, index) => (

            
            <input
            key={index}
            style={{ width: `${width}`, background: "#f9f9f9" }}
              type={types[index]}
              className={`login_input small-placeholder prefixInputs ${
                disabled ? "disabled" : ""
              }`}
              placeholder={placeholders[index]}
              name={name}
              defaultValue={values[index]}
              onWheel={(event) => event.currentTarget.blur()}
              onChange={(e) => {
                if (onChange) {
                  const updatedValues = [...values];
                  updatedValues[index] = e.target.value;
                  const body = {
                    target: {
                      name,
                      value: updatedValues,
                    },
                  };
                  onChange(body);
                }
              }}
              autoComplete={"off"}
              maxLength={maxLength}
              disabled={disabled}
            />
            

        ))}
      </div>
    </div>
  );
};

export default SequenceInput;
